import Vue from 'vue'
import Router from 'vue-router'
// import RoomsList from '@/views/RoomsList'
// import Index from '@/views/Index'

const Index = resolve => require(['@/views/Index.vue'], resolve)
const RoomsList = resolve => require(['@/views/RoomsList.vue'], resolve)
const PostsPage = resolve => require(['@/views/PostsPage.vue'], resolve)
const PostDetail = resolve => require(['@/views/PostDetailPage.vue'], resolve)

Vue.use(Router)


export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'RoomsList',
      component: RoomsList,
      meta: {
        keepAlive: true // 需要被缓存
      }
    },
    {
      path: '/info',
      name: 'Index',
      component: Index,
      meta: {
        keepAlive: true // 需要被缓存
      }
    },
    {
      path: '/post',
      name: 'PostsPage',
      component: PostsPage,
      meta: {
        keepAlive: false// 不需要被缓存
      }
    },
    {
      path: '/post/:postId',
      name: 'PostDetail',
      component: PostDetail,
      meta: {
        keepAlive: false
      }
    }
  ]
})



